.main-card {
  margin: 30px;
  border: 1px solid $mainColor;
  border-radius: 5px;
  box-shadow: 0 5px 10px 0 rgba(34, 88, 117, 0.7);

  .presentation {
    padding: 30px 15px;
    color: $mainColor;
    text-align: center;
    letter-spacing: 1px;
    font-size: 1rem;
    line-height: 1.25rem;
    @include transition; } }

.divider {
  @include divider; }
