.navbar {
  @include flex;
  justify-content: flex-end;
  align-items: center;
  padding: 0 30px;
  border-bottom: 3px solid $mainColor; }

.logo {
  width: 150px;
  margin-right: auto;
  img {
    width: 100%; } }

.hamburger-menu {
  width: 2rem;
  cursor: pointer;
  display: inline-block;
  align-self: center;
  margin-right: 0;

  .bar {
    background: $mainColor;
    width: 100%;
    height: 5px;
    margin: 6px 0;
    border-radius: 5px; } }
